import { Campaigns } from '@hooks/giftCampaigns/controller';
import { ParsedUrlQuery } from '@interfaces/index';
import { ElementType } from '@utils/typeUtils';

export function buildParamsByQuery(query: ParsedUrlQuery = {}) {
  return Object.keys(query).reduce((acc: ParsedUrlQuery, key) => {
    const queryValue = query[key];
    const param: ParsedUrlQuery = {};
    if (key === 'q') param[key] = queryValue;
    else param[key] = Array.isArray(queryValue) ? queryValue : queryValue.split(',');
    return { ...acc, ...param };
  }, {});
}

export const calculateMultiplyBy = (promotion: ElementType<Campaigns>): number => {
  const multiplyBy = promotion.type === 'Type A' ? promotion.multiplyBy : promotion.multiplyByTypeB || 0;
  return multiplyBy;
};
