import React, { Fragment, useEffect, memo } from 'react';
import { Empty, Divider } from 'antd';
import cn from 'classnames';

import { GetVariantSearchQuery } from '@hooks/search';
import { useCampaigns } from '@hooks/giftCampaigns/controller';
import { ElementType } from '@utils/typeUtils';
import { toBottomUnavailVariants } from '@utils/variant';
import { getUniqueCampaignIdsFromVariants } from '@utils/giftCampaign';

import SearchResultItem from './SearchResultItem';
import SearchResultSkeleton from '@components/Skeleton/SearchResultSkeleton';

import s from './SearchResult.module.less';

export type Variants = Exclude<GetVariantSearchQuery['variantSearch']['results']['nodes'], null | undefined>;
type OrderItemsWithQuantity = Array<{
  variant: {
    id: string;
  };
  quantity: number;
  orderItemId: string;
  vatWholesalePrice: number;
}>;
export type Variant = ElementType<Variants>;

export type SearchResultProps = {
  variants: Variants;
  loading?: boolean;
  orderItemsWithQuantity?: OrderItemsWithQuantity;
  searchId?: string;
  options?: {
    noAddToCart: boolean;
    linkItemToSearchList: boolean;
    searchTerm: string;
    setShowResult: React.Dispatch<React.SetStateAction<boolean>>;
    isWithPromotions: boolean;
    isWithWholesalePrice: boolean;
  };
  className?: string;
};

const SearchResult = memo(
  ({ variants, loading, orderItemsWithQuantity, options, searchId, className }: SearchResultProps) => {
    const { setCampaignIds } = useCampaigns();

    const campaignIds = getUniqueCampaignIdsFromVariants(variants) as string[];
    const searchResults = toBottomUnavailVariants(variants).map((variant, index, array) => {
      const inCartQuantity = (orderItemsWithQuantity || []).find(item => variant?.id === item?.variant?.id)?.quantity;
      return (
        <Fragment key={variant?.id}>
          <SearchResultItem variant={variant} options={options} inCartQuantity={inCartQuantity} searchId={searchId} />
          {index < array.length - 1 && <Divider className={s.divider} />}
        </Fragment>
      );
    });

    useEffect(() => {
      setCampaignIds?.(campaignIds);
    }, [campaignIds]);

    return (
      <div className={cn(s.root, className)} onClick={() => options?.setShowResult(false)}>
        <div className={s.resultWrapper}>
          {loading ? (
            <SearchResultSkeleton />
          ) : !variants?.length ? (
            <Empty description="Khng tm thy sn phm. Xin vui lng th li vi t kha khc" className={s.empty} />
          ) : (
            searchResults
          )}
        </div>
      </div>
    );
  },
);

export default SearchResult;
