import React from 'react';
import { Button } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import Link from 'next/link';
import { useRouter } from 'next/router';
import Image from 'next/image';

import PromotionSelection from '@components/InstantDeal/PromotionSelection';
import FirstOrderDiscountSuggestion from '@components/InstantDeal/FirstOrderDiscountSuggestion';
import InlineWarning from '@components/InlineWarning';
import { toVNDShort } from '@utils/formatter';
import { getVariantCampaignsFromCampaigns } from '@utils/giftCampaign';
import { getCurrPage } from '@utils/search';
import { useAuth } from '@context/AuthContext';
import { DiscountDetails } from '@hooks/variant';
import { useSearchConversionMutation } from '@hooks/search';
import { useCampaigns } from '@hooks/giftCampaigns/controller';

import { SearchResultProps, Variant } from './SearchResult';

import s from './SearchResult.module.less';

const dummyImage = 'https://dummyimage.com/46x46/e1e1e1/fff';

type SearchResultItemProps = {
  variant: Variant;
  inCartQuantity?: number;
  options?: SearchResultProps['options'];
  searchId?: string;
};

const SearchResultItem = ({ variant, inCartQuantity = 0, options, searchId }: SearchResultItemProps) => {
  const { isLoggedIn } = useAuth();
  const { campaigns } = useCampaigns();
  const { pathname } = useRouter();
  const [searchConvert] = useSearchConversionMutation();

  const variantCampaignIds = variant?.campaignIds || [];
  const variantGiftCampaigns = getVariantCampaignsFromCampaigns(campaigns || [], variantCampaignIds);
  const hasFirstOrderDiscount = variant && !!variant.firstOrderDiscount;

  function handleClickResult() {
    variant?.id &&
      searchId &&
      searchConvert({
        variables: {
          convertableType: 'Variant',
          convertableId: variant.id,
          searchId,
          source: getCurrPage(pathname),
        },
      });
  }

  return (
    <Link
      href={
        options?.linkItemToSearchList
          ? `/instantdeal?q=${options?.searchTerm}&variantId=${variant?.id}`
          : `/products/${variant?.product.id}?optionValueIds=${variant?.optionValues
              .map(option => option.id)
              .join('-')}`
      }
      key={variant?.id}
      passHref
    >
      <a onClick={handleClickResult}>
        <div className={s.resultItemWrapper}>
          <div className={s.resultItem}>
            <div className={s.image}>
              <Image src={variant?.imageUrls[0] || variant?.defaultImageUrl || dummyImage} width={46} height={46} />
            </div>
            <div className={s.resultItemInfo}>
              <span className={s.resultItemName}>
                {variant?.name || ''} - {variant?.product.brand?.name}
              </span>
              {!variant?.available && <InlineWarning text="Sn phm ngng kinh doanh" />}
              <p className={s.resultItemAdditionalInfo}>
                Thng hiu: {variant?.product.brand?.name} | M hng:{' '}
                <span className={s.materialNumber}>{variant?.materialNumber}</span> | Gi i l:{' '}
                {toVNDShort(Number(variant?.vatWholesalePrice))}
              </p>
              {options?.isWithPromotions &&
                (hasFirstOrderDiscount ? (
                  <FirstOrderDiscountSuggestion
                    firstOrderDiscount={variant?.firstOrderDiscount as DiscountDetails}
                    currQuantity={inCartQuantity}
                  />
                ) : (
                  <PromotionSelection variantGiftCampaigns={variantGiftCampaigns} options={{ textOnly: true }} />
                ))}
            </div>
          </div>
          {isLoggedIn && !options?.noAddToCart && (
            <Button icon={<PlusOutlined />} type="text" className={s.btnAddCart}>
              Thm
            </Button>
          )}
        </div>
      </a>
    </Link>
  );
};

export default SearchResultItem;
