import React, { memo } from 'react';

import { useAppLayout } from '@context/LayoutContext';

import DesktopBasicLayout from './BasicLayout';
import MobileBasicLayout from './Mobile';
import { BasicLayoutProps, MobileBasicLayoutProps } from './type';

const BasicLayout = memo((props: BasicLayoutProps & MobileBasicLayoutProps) => {
  const { isMobile } = useAppLayout();
  const LayoutComponent = isMobile ? MobileBasicLayout : DesktopBasicLayout;

  return <LayoutComponent {...props} />;
});

export default BasicLayout;
