import React from 'react';
import { Skeleton } from 'antd';

import s from './SearchResultSkeleton.module.less';

const SearchResultItemSkeleton = () => {
  return (
    <div className={s.item}>
      <div className={s.imageContainer}>
        <Skeleton.Image className={s.image} />
      </div>
      <div className={s.infoContainer}>
        <Skeleton active paragraph={{ rows: 1 }} />
      </div>
      <div className={s.actionContainer}>
        <Skeleton.Button size="small" />
      </div>
    </div>
  );
};

const SearchResultSkeleton = () => {
  const searchResultsSkeleton = new Array(5).fill(0).map((_, idx) => <SearchResultItemSkeleton key={idx} />);
  return <div className={s.root}>{searchResultsSkeleton}</div>;
};

export default SearchResultSkeleton;
