import React from 'react';
import { Input } from 'antd';
import classnames from 'classnames';

import SearchResult from './SearchResult';

import useSearchBar from './useSearchBar';
import s from './index.module.less';

const { Search } = Input;

const Overlay = ({ onClose }: { onClose: () => void }) => <div onClick={onClose} className={s.overlay} />;

const SearchBar = () => {
  const { searchRef, showResult, value, loading, data, setShowResult, handleFocus, handleChange, handleSearch } =
    useSearchBar();

  return (
    <>
      <div className={classnames(s.root, showResult && s.zIndexHigh)}>
        <Search
          value={value}
          defaultValue={value}
          onChange={handleChange}
          onSearch={handleSearch}
          onFocus={handleFocus}
          ref={searchRef}
          placeholder="Tm tn sn phm hoc m hng"
          enterButton
          size="large"
          allowClear
          className={s.searchInput}
        />
        {showResult && (
          <SearchResult
            loading={loading}
            variants={data?.variantSearch?.results.nodes || []}
            searchId={data?.variantSearch?.searchId}
          />
        )}
      </div>
      {showResult && (
        <Overlay
          onClose={() => {
            setShowResult(false);
          }}
        />
      )}
    </>
  );
};

export default SearchBar;
