export function getCurrPage(pathname: string) {
  const routeMap = new Map([
    ['/', 'HomePage'],
    ['/instantdeal', 'InstantDeal'],
  ]);

  let result = 'Other';
  routeMap.forEach((value, key) => {
    // TODO: write a regexp instead of pathname === key to match more complex route
    if (pathname === key) result = value;
  });

  return result;
}
