import get from 'lodash/get';
import { GiftCampaign, GiftConditionItem } from '@hooks/giftCampaign';
import { NoncombinableGiftCampaign, LevelableGiftCampaign } from '@hooks/cart';
import { Maybe } from '@graphql/types.generated';
import {
  getGiftsDataNoncombinableCampaigns,
  getGiftsDataGiftCampaigns,
  getGiftsDataLevelableCampaigns,
} from './orderItem';
import { Campaigns } from '@hooks/giftCampaigns/controller';
import { GetOrderItemQuery } from '@hooks/orderItem';
import { Product } from '@components/InstantDeal/ProductCategoriesDropdown';
import { Variants } from '@components/SearchBar/SearchResult';

/* document https://coderpush.slite.com/app/channels/W6Vtt1eO8e/notes/Gkqnv5RfK
- 1.1: 1 variant -> 1 gift
- 1.2: 1 variant -> n gifts (or)
- 1.3: 1 variant -> n gifts (and)

- 2.1: 1 || n variants (any) -> 1 gift
- 2.2: 1 || n variants (any) -> n gifts (or)
- 2.3: 1 || n variants (any) -> n gift (and)
- 2.4: n variants (and) -> 1 gift
- 2.5: n variants (and) -> n gifts (or)
- 2.6: n variants (and) -> n gift (and)

*/
export function getCampaignType(promotion?: GiftCampaign) {
  const requirements = promotion?.requirements || [];
  const gifts = promotion?.gifts || [];
  const giftSelectionItemGroups = gifts[0]?.giftSelectionItemGroups || [];

  const isMultipleRequirements = requirements.length > 1;

  const isRequirementHasMultipleConditions = (promotion?.conditionCount || 0) > 1;

  const isSelectionGift = gifts.length > 1; // or
  const isGiftHasMultipleGroups = giftSelectionItemGroups.length > 1; // and
  const isGiftHasMultipleItems = (giftSelectionItemGroups[0]?.giftSelectionItems?.length || 0) > 1; // and

  const isRequiredMultipleVariants = isMultipleRequirements || isRequirementHasMultipleConditions;
  const isMultipleGifts = isSelectionGift || isGiftHasMultipleGroups || isGiftHasMultipleItems;

  const isAnyVariants =
    Boolean(promotion?.requirements?.[0]?.totalQuantityThreshold) ||
    Boolean(promotion?.requirements?.[0]?.totalRevenueThreshold);

  if (isAnyVariants) {
    if (isMultipleGifts) {
      if (isSelectionGift) return 2.2;
      return 2.3;
    } else return 2.1;
  }

  if (isRequiredMultipleVariants) {
    if (isMultipleGifts) {
      if (isSelectionGift) return 2.5;
      return 2.6;
    } else return 2.4;
  } else {
    if (isMultipleGifts) {
      if (isSelectionGift) return 1.2;
      return 1.3;
    } else return 1.1;
  }
}

export const caculateMetaDataByVariantId = (variantId?: string, promotion?: GiftCampaign, _variantPrice?: number) => {
  const campaignType = getCampaignType(promotion);
  const currentCondition = promotion?.requirements?.[0]?.giftConditionItems?.find(
    condition => condition.variant.id === variantId,
  );
  const variantPrice = currentCondition?.variant?.netPrice || _variantPrice;

  /* this case is different, it used totalRevenueThreshold or totalQuantityThreshold of requirements */
  if (campaignType >= 2.1 && campaignType <= 2.3) {
    return caculateWithAnyVariants({ currentCondition, promotion, variantPrice });
  }

  return caculateForEachVariants({ currentCondition, promotion, variantPrice });
};

type Keys = {
  quantityThresholdOrRevenueThresholdKey: 'quantityThreshold' | 'revenueThreshold';
  quantityOrRevenueKey: 'quantity' | 'revenue';
  totalQuantityThresholdOrRevenueThresholdKey: 'totalQuantityThreshold' | 'totalRevenueThreshold';
};

export const keyFactory = (isRequireQuantity: boolean): Keys => ({
  quantityThresholdOrRevenueThresholdKey: isRequireQuantity ? 'quantityThreshold' : 'revenueThreshold',
  quantityOrRevenueKey: isRequireQuantity ? 'quantity' : 'revenue',
  totalQuantityThresholdOrRevenueThresholdKey: isRequireQuantity ? 'totalQuantityThreshold' : 'totalRevenueThreshold',
});

type CaculateMetaData = {
  currentCondition?: GiftConditionItem;
  promotion?: GiftCampaign;
  variantPrice?: number;
};

const caculateForEachVariants = ({ currentCondition, promotion, variantPrice }: CaculateMetaData) => {
  const isRequireQuantity = !currentCondition?.revenueThreshold;
  const { quantityThresholdOrRevenueThresholdKey, quantityOrRevenueKey } = keyFactory(isRequireQuantity);
  const currentProgressData = promotion?.progressData?.find(item => item.id === currentCondition?.variant?.id);

  // revenue is string we need to conver to number
  const currentValue = currentProgressData?.[quantityOrRevenueKey]
    ? Number(currentProgressData?.[quantityOrRevenueKey])
    : 0;
  const requirementThreshold = currentCondition?.[quantityThresholdOrRevenueThresholdKey];

  const currentQuantity =
    promotion?.progressData?.find(item => item.id === currentCondition?.variant?.id)?.quantity || 0;
  const requirementRevenueThreshold = currentCondition?.revenueThreshold || 0;

  const requirementQuantity =
    currentCondition?.quantityThreshold || Math.round(requirementRevenueThreshold / (variantPrice || 1)) || 0;

  const percent = ((currentValue || 0) / (requirementThreshold || 1)) * 100;
  const quantityRemaining = requirementQuantity - currentQuantity;
  const validQuantityRemaining = quantityRemaining < 0 ? 0 : quantityRemaining;

  return {
    giftCondition: currentCondition,
    isRequireQuantity,
    currentValue,
    requirementThreshold,
    progress: percent > 100 ? 100 : percent.toFixed(2),
    quantityRemaining: validQuantityRemaining,
    quantityRemainingText: `Mua thm ${validQuantityRemaining} ci`,
    currentQuantity,
  };
};

const caculateWithAnyVariants = ({ currentCondition, promotion, variantPrice }: CaculateMetaData) => {
  const requirement = promotion?.requirements[0];
  const isRequireQuantity = !requirement?.totalRevenueThreshold;
  const { totalQuantityThresholdOrRevenueThresholdKey, quantityOrRevenueKey } = keyFactory(isRequireQuantity);

  const requirementThreshold = requirement?.[totalQuantityThresholdOrRevenueThresholdKey];

  // currentValue is total quantity or revenue required items
  const currentValue =
    promotion?.progressData?.reduce((acc: number, item) => {
      acc += item?.[quantityOrRevenueKey] ? Number(item?.[quantityOrRevenueKey]) : 0;
      return acc;
    }, 0) || 0;

  const currentQuantity = isRequireQuantity
    ? promotion?.progressData?.reduce((acc: number, item) => {
        acc += item.quantity || 0;
        return acc;
      }, 0) || 0
    : Math.floor(currentValue / (variantPrice || 1));

  const requirementRevenueThreshold = requirement?.totalRevenueThreshold || 0;

  const requirementQuantity =
    requirement?.totalQuantityThreshold || Math.round(requirementRevenueThreshold / (variantPrice || 1)) || 0;

  const percent = ((currentValue || 0) / (requirementThreshold || 1)) * 100;
  const quantityRemaining = requirementQuantity - currentQuantity;
  const validQuantityRemaining = quantityRemaining < 0 ? 0 : quantityRemaining;

  return {
    giftCondition: currentCondition,
    isRequireQuantity,
    currentValue,
    requirementThreshold,
    progress: percent > 100 ? 100 : percent.toFixed(2),
    quantityRemaining: validQuantityRemaining,
    quantityRemainingText: `Mua thm ${validQuantityRemaining} ci`,
    currentQuantity,
  };
};

export type ProgressItem = {
  id: string;
  materialNumber?: Maybe<string>;
  name: string;
  quantity: number;
  revenue: any;
};

export type ConditionItem = {
  id: string;
  variantId: string;
  name: string;
  materialNumber: string;
  revenueThreshold?: Maybe<string>;
  quantityThreshold?: Maybe<number>;
  vatWholesalePrice: Maybe<number>;
  available: boolean;
};

export type SynchronizedProgressData = Maybe<ProgressItem>[];

export const syncProgressDataWithCondition = (
  progressItems: ProgressItem[],
  conditionItems: ConditionItem[],
): SynchronizedProgressData => {
  if (progressItems.length === conditionItems.length) return progressItems;

  const synchronizedProgressItems = new Array(conditionItems.length).fill(null);

  conditionItems.forEach((item, index) => {
    for (let i = 0; i < progressItems.length; i++) {
      if (item.variantId === progressItems[i].id) {
        synchronizedProgressItems[index] = progressItems[i];
        break;
      }
    }
  });

  return synchronizedProgressItems;
};

export const getAllGiftsFromCampaigns = ({
  noncombinableGiftCampaigns,
  giftCampaigns,
  levelableGiftCampaigns,
}: {
  noncombinableGiftCampaigns: NoncombinableGiftCampaign[] | null;
  giftCampaigns: GiftCampaign[] | null;
  levelableGiftCampaigns: LevelableGiftCampaign[] | null;
}) => {
  const noncombinableGiftsData = getGiftsDataNoncombinableCampaigns(noncombinableGiftCampaigns || []) || [];
  const normalGiftsData = getGiftsDataGiftCampaigns(giftCampaigns || []) || [];
  const levelableGiftsData = getGiftsDataLevelableCampaigns(levelableGiftCampaigns || []) || [];

  return [...noncombinableGiftsData, ...normalGiftsData, ...levelableGiftsData];
};

export const getPropratedPriceByVariantId = (variantId?: string, promotion?: GiftCampaign) => {
  const giftConditionItem = promotion?.requirements?.[0]?.giftConditionItems?.find(
    condition => condition.variant.id === variantId,
  );

  return giftConditionItem?.propratedPrice || 0;
};

type AllCampaigns = {
  giftCampaigns: GiftCampaign[] | null;
  levelableGiftCampaigns: LevelableGiftCampaign[] | null;
  noncombinableGiftCampaigns: NoncombinableGiftCampaign[] | null;
};

export function getGiftCampaigns(giftCampaigns: GiftCampaign[]) {
  return giftCampaigns.reduce(
    (acc, campaign) => (Number(campaign.totalRewardsInCart) > 0 && campaign.applied ? [...acc, campaign] : acc),
    [] as GiftCampaign[],
  );
}

export function getLevelCampaigns(campaigns: LevelableGiftCampaign[], array?: GiftCampaign[]) {
  const typeBCampaigns = getGiftCampaigns(campaigns);
  campaigns.forEach(campaign => {
    if (campaign.giftCampaignChildren) array = getLevelCampaigns(campaign.giftCampaignChildren, array);
    array = [...(array || []), ...typeBCampaigns];
  });

  return array || [];
}

export function getNoncombinableCampaigns(noncombinableCampaigns: NoncombinableGiftCampaign[]) {
  return noncombinableCampaigns.reduce((acc, campaign) => {
    const typeBCampaigns = getGiftCampaigns(campaign.giftCampaignGroups || []);

    acc.push(...typeBCampaigns);

    return acc;
  }, [] as GiftCampaign[]);
}

export function getAllCampaigns({ giftCampaigns, levelableGiftCampaigns, noncombinableGiftCampaigns }: AllCampaigns) {
  return [
    ...getGiftCampaigns(giftCampaigns || []),
    ...getLevelCampaigns(levelableGiftCampaigns || []),
    ...getNoncombinableCampaigns(noncombinableGiftCampaigns || []),
  ];
}

export const getVariantCampaignsFromCampaigns = (campaigns: Campaigns, variantCampaignIds: string[]) => {
  return campaigns.filter(campaigns => variantCampaignIds.includes(campaigns.id));
};

export const getUniqueCampaignIdsFromProducts = (products: Product[]) => {
  let campaignIds: string[] = [];
  products.forEach(product => {
    campaignIds = campaignIds.concat(product.defaultVariant.campaignIds);
  });

  return [...new Set(campaignIds)];
};

export const getUniqueCampaignIdsFromVariants = (variants: Variants) => {
  const campaignIds = variants?.map(variant => variant?.campaignIds).reduce((acc, ids) => acc?.concat(ids || []), []);

  return [...new Set(campaignIds)];
};

export function isVariantOrderItem(selectedVariantId: string, orderItem?: GetOrderItemQuery) {
  return selectedVariantId === get(orderItem, 'orderItem.variant.id', '');
}

export function isOrderItemCampaign(campaigns: GiftCampaign[], orderItem?: GetOrderItemQuery, variantId?: string) {
  return campaigns && campaigns.length && isVariantOrderItem(variantId || '', orderItem);
}
