import React, { useMemo } from 'react';
import { ApolloError, QueryLazyOptions } from '@apollo/client';
import deepmerge from 'deepmerge';
import { ITEM_PER_PAGE } from '@utils/constants';

import { useGetVariantSearchLazyQuery, GetVariantSearchQuery } from './search.gql.generated';
import { Exact, Maybe } from '@graphql/types.generated';
import { message } from 'antd';

type Params = {
  after?: string;
  first?: number;
  query?: string;
  optionValueIds?: string[];
  brandIds?: string[];
  categoryIds?: string[];
  attributes?: string[];
};

type SearchContextType = {
  data?: GetVariantSearchQuery;
  loading: boolean;
  error?: ApolloError;
  fetchVariantSearch: (
    options?: QueryLazyOptions<
      Exact<{
        query: string;
        first?: Maybe<number>;
        after?: Maybe<string>;
        optionValueIds?: Maybe<string | string[]>;
        brandIds?: Maybe<string | string[]>;
        categoryIds?: Maybe<string | string[]>;
        attributes?: Maybe<string | string[]>;
        source: string;
      }>
    >,
  ) => void;
  handleFetchMoreVariantSearch: (params: Params) => void;
};

type SearchControllerProps = {
  children: React.ReactNode;
};

const SearchContext = React.createContext<SearchContextType>({
  loading: false,
  handleFetchMoreVariantSearch: () => null,
  fetchVariantSearch: () => null,
});

export const SearchController = ({ children }: SearchControllerProps) => {
  const [fetchVariantSearch, { loading, data, fetchMore }] = useGetVariantSearchLazyQuery({
    notifyOnNetworkStatusChange: true,
  });

  const handleFetchMoreVariantSearch = React.useCallback(
    (params: Params = {}) => {
      try {
        fetchMore?.({
          variables: {
            query: params.query || '',
            first: ITEM_PER_PAGE,
            ...params,
          },
          updateQuery: (
            prev: GetVariantSearchQuery,
            {
              fetchMoreResult,
            }: {
              fetchMoreResult?: GetVariantSearchQuery;
            },
          ): GetVariantSearchQuery => {
            if (!fetchMoreResult?.variantSearch) {
              return prev;
            }
            return deepmerge(prev, fetchMoreResult);
          },
        });
      } catch (error: any) {
        message.error(error?.message);
      }
    },
    [fetchMore],
  );

  const context = useMemo(
    () => ({
      data,
      loading,
      fetchVariantSearch,
      handleFetchMoreVariantSearch,
    }),
    [data, loading],
  );

  return (
    <SearchContext.Provider value={context}>
      {typeof children === 'function' ? children(context) : children}
    </SearchContext.Provider>
  );
};

export function useSearch() {
  return React.useContext(SearchContext);
}
