import React, { memo } from 'react';
import { Layout } from 'antd';
import Head from 'next/head';
import cn from 'classnames';

import MobileHeader from '@layouts/Header/Mobile';
import MobileBottomNavigator from '@components/Mobile/BottomNavigator';
import { CartController } from '@hooks/cart';

import type { MobileBasicLayoutProps } from '../type';
import s from './index.module.less';

const { Content } = Layout;

const MobileBasicLayout = memo(
  ({
    title = 'This is the default title',
    children,
    showBottomNavigator,
    showBackButton,
    backButtonTitle,
    backButtonLink,
    pageTitle,
    hideCart,
    className,
  }: MobileBasicLayoutProps) => {
    return (
      <div>
        <Head>
          <title>{title}</title>
          <meta charSet="utf-8" />
          <meta name="viewport" content="initial-scale=1.0, width=device-width" />
        </Head>
        <Layout className={s.root}>
          {/* TODO: CartController should be in _app.tsx */}
          <CartController>
            <MobileHeader
              pageTitle={pageTitle}
              backButtonTitle={backButtonTitle}
              backButtonLink={backButtonLink}
              showBackButton={showBackButton}
              hideCart={hideCart}
            />
          </CartController>
          <Content className={cn(s.content, className)}>{children}</Content>
          {showBottomNavigator && <MobileBottomNavigator />}
        </Layout>
      </div>
    );
  },
);

export default MobileBasicLayout;
