import { OrderItem, NoncombinableGiftCampaign, LevelableGiftCampaign, SelectedGift } from '@hooks/cart';
import { GiftCampaign } from '@graphql/types.generated';
import isEmpty from 'lodash/isEmpty';
import flow from 'lodash/flow';
import { OrderItemElement } from '@utils/gift';
import { ExtendedGiftSelection } from '@components/SelectGiftModal';
import { OrderGiftItemCombined } from '@components/Cart/types';
import { getSelectedGiftsCampaigns, getSelectedGiftsNoncombinable, getSelectedGiftsLevelCampaigns } from './selectGift';
import { generateId } from '.';
import { getGiftItemNumber } from './giftItem';

function getGiftsFromCampaign(campaign: GiftCampaign): ExtendedGiftSelection[] {
  if (!campaign) return [];
  const {
    applied,
    multiplyBy,
    canCheckOutRewards,
    totalRewardsInCart,
    remainingRewardTimesInCart,
    gifts,
    type,
    selectedGifts,
    selectedGiftsInCart,
    id: campaignId,
    remainingRewardTimes,
    importableSameOrder,
    campaignName,
  } = campaign;
  return gifts.map(gift => ({
    ...gift,
    campaignName,
    remainingRewardTimes,
    remainingRewardTimesInCart,
    importableSameOrder,
    isAccumulated: type === 'Type B',
    multiplyBy,
    canCheckOutRewards,
    totalRewardsInCart,
    selectedGiftInCart: selectedGiftsInCart?.find(selected => selected.giftSelection.id === gift.id) || undefined,
    selectedGift: selectedGifts?.find(selectedGift => selectedGift.giftSelection.id === gift.id) || undefined,
    campaignId,
    applied,
  }));
}

function noSelectionGifts(gifts: ExtendedGiftSelection[]) {
  if (gifts.length === 0) return [];
  return gifts.map((gift, _, array) => {
    const { giftSelectionItemGroups, isAccumulated } = gift;
    if (!isAccumulated && array.length === 1 && giftSelectionItemGroups.length === 1) {
      return { ...gift, noSelection: true };
    }
    return gift;
  });
}

export function getGiftsDataGiftCampaigns(giftCampaigns: GiftCampaign[]) {
  return giftCampaigns.reduce((acc, campaign) => {
    const gifts = flow([getGiftsFromCampaign, noSelectionGifts])(campaign || []);
    acc.push(...gifts);
    return acc;
  }, [] as ExtendedGiftSelection[]);
}

export function getGiftsDataLevelableCampaigns(
  levelableGiftCampaigns: LevelableGiftCampaign[],
  array?: ExtendedGiftSelection[],
) {
  levelableGiftCampaigns.forEach(campaign => {
    const newGifts = getGiftsFromCampaign(campaign) || [];
    if (campaign.giftCampaignChildren) array = getGiftsDataLevelableCampaigns(campaign.giftCampaignChildren, array);
    array = [...(array || []), ...newGifts];
  });

  return array;
}

export function getGiftsDataNoncombinableCampaigns(noncombinableCampaigns: NoncombinableGiftCampaign[]) {
  if (!noncombinableCampaigns) return [];

  function giftsFromNoncombinableGroups(giftCampaignGroups: GiftCampaign[]) {
    let gifts: ExtendedGiftSelection[] = [];
    for (const item in giftCampaignGroups) {
      gifts = gifts.concat(getGiftsFromCampaign(giftCampaignGroups[item]));
    }

    return gifts;
  }

  const allGifts: ExtendedGiftSelection[] = [];
  noncombinableCampaigns.forEach(item => {
    const group: any = giftsFromNoncombinableGroups(item?.giftCampaignGroups || []);

    group && !isEmpty(group) && allGifts.push(...group);
  });

  return allGifts;
}

export function getAllGiftsData({
  noncombinableGiftCampaigns,
  giftCampaigns,
  levelableGiftCampaigns,
}: {
  levelableGiftCampaigns?: LevelableGiftCampaign[] | null;
  giftCampaigns?: GiftCampaign[] | null;
  noncombinableGiftCampaigns?: NoncombinableGiftCampaign[] | null;
}) {
  const noncombinableGiftsData = getGiftsDataNoncombinableCampaigns(noncombinableGiftCampaigns || []) || [];
  const normalGiftsData = getGiftsDataGiftCampaigns(giftCampaigns || []) || [];
  const levelableGiftsData = getGiftsDataLevelableCampaigns(levelableGiftCampaigns || []) || [];

  return [...noncombinableGiftsData, ...normalGiftsData, ...levelableGiftsData];
}

export function getPrioritizedCampaignId(orderItem: OrderItem): string {
  const { firstGiftCampaignId, firstLevelableCampaignId, firstNoncombinableCampaignId } = orderItem;

  return firstNoncombinableCampaignId || firstLevelableCampaignId || firstGiftCampaignId || '';
}

// TODO: carefully remove this logic because this function groupOrderItemsByPrioritizedCampaignId serve the purpose
export function groupOrderItemsByCampaignId(transformedOrderItems: OrderItemElement[]): {
  list: OrderItemElement[];
  indexMap: Map<string, number>;
} {
  const giftsMap: { [key: string]: OrderItemElement[] } = {};
  const prioritizedIds = new Set();
  const collection: OrderItemElement[] = [];

  transformedOrderItems.forEach(orderItem => {
    const { prioritizedId: id, campaignId } = orderItem;
    id && prioritizedIds.add(id);
    if (prioritizedIds.has(campaignId)) {
      giftsMap[campaignId] = giftsMap[campaignId] ? [...giftsMap[campaignId], orderItem] : [orderItem];
    } else {
      collection.push(orderItem);
    }
  });

  const indexMap: Map<string, number> = new Map();
  collection.forEach(item => {
    if (item?.prioritizedId && giftsMap[item.prioritizedId]) {
      const id = String(item.prioritizedId);
      indexMap.set(id, item.index);
    }
  });

  const list = collection.reduce((acc, item) => {
    acc.push(item);
    const { prioritizedId: id } = item;
    if (item.index === indexMap.get(String(id))) {
      acc.push(...(giftsMap[id || 0] || []));
    }
    return acc;
  }, [] as OrderItemElement[]);

  return { list, indexMap };
}

function getPrioritizedIdMap(orderItems: OrderItem[]) {
  const map = new Map();

  orderItems.forEach(orderItem => {
    const {
      firstGiftCampaignId,
      firstLevelableCampaignId,
      firstNoncombinableCampaignId,
      giftCampaigns,
      levelableGiftCampaigns,
      noncombinableGiftCampaigns,
    } = orderItem;
    if (firstGiftCampaignId) {
      const campaign = giftCampaigns?.find(campaign => campaign.id === firstGiftCampaignId);
      campaign && map.set(firstGiftCampaignId, campaign);
    }
    if (firstLevelableCampaignId) {
      const campaign = levelableGiftCampaigns?.find(campaign => campaign.id === firstLevelableCampaignId);
      campaign && map.set(firstLevelableCampaignId, campaign);
    }
    if (firstNoncombinableCampaignId) {
      const campaign = noncombinableGiftCampaigns?.find(campaign => campaign.groupId === firstNoncombinableCampaignId);
      campaign && map.set(firstNoncombinableCampaignId, campaign);
    }
  });

  return map;
}

function generateDummyGroupItem(campaign: GiftCampaign & { giftCampaignGroups?: GiftCampaign[]; groupId: string }) {
  const { id, selectedGifts, giftCampaignGroups, selectedGiftsInCart, groupId, type } = campaign;
  const currSelectedGifts = type === 'Type A' ? selectedGifts : selectedGiftsInCart;
  const allSelectedGifts = giftCampaignGroups
    ? giftCampaignGroups.reduce(
        (acc, group) =>
          (group.selectedGifts || []).length > 0 || (group.selectedGiftsInCart || []).length > 0
            ? [...acc, ...(group.selectedGifts || []), ...(group.selectedGiftsInCart || [])]
            : acc,
        [] as SelectedGift[],
      )
    : currSelectedGifts || [];

  return {
    id: `orderItem_${id}_${generateId()}`,
    campaignId: groupId ? groupId : id,
    receivedGift: true,
    isNoncombinable: Boolean(groupId),
    giftItemNumber: getGiftItemNumber(allSelectedGifts),
  };
}

export function groupOrderItemsByPrioritizedCampaignId(orderItems: OrderGiftItemCombined[]) {
  if (orderItems && orderItems.length === 0) return [];
  const campaignGroupIdMap = getPrioritizedIdMap(orderItems);
  const orderItemObj: { [key: string]: any[] } = {};
  const noGroupItem: OrderGiftItemCombined[] = [];

  // pick out all the items of a group and add it to a data structure
  orderItems.forEach(orderItem => {
    const prioritizedId = getPrioritizedCampaignId(orderItem);
    if (campaignGroupIdMap.has(prioritizedId)) {
      const value = orderItemObj[prioritizedId];
      orderItemObj[prioritizedId] = value ? [...value, orderItem] : [orderItem];
    } else {
      noGroupItem.push(orderItem);
    }
  });

  // go over the data structure and add a dummy group item to it
  for (const item in orderItemObj) {
    const campaign = campaignGroupIdMap.get(item);
    const dummyGroupItem = campaign ? generateDummyGroupItem(campaign) : null;
    if (campaign.type === 'Type A') {
      dummyGroupItem && (campaign.applied || campaign.giftCampaignGroups) && orderItemObj[item].push(dummyGroupItem);
    } else {
      dummyGroupItem && orderItemObj[item].push(dummyGroupItem);
    }
  }

  // check the first item, if it has correct group id, attach the group items
  // if not just append them
  const result = [];
  const firstItem = orderItems[0];
  const prioritizedId = getPrioritizedCampaignId(firstItem);
  if (campaignGroupIdMap.has(prioritizedId)) {
    result.push(orderItemObj[prioritizedId]);
    delete orderItemObj[prioritizedId];
  }
  for (const item in orderItemObj) {
    result.push(orderItemObj[item]);
  }

  result.push(...noGroupItem);

  return result;
}

export function getSelectedGiftsOrderItems(orderItems: OrderItem[]) {
  return orderItems.reduce((acc, item) => {
    const { giftCampaigns, noncombinableGiftCampaigns, levelableGiftCampaigns } = item;
    const selectedGiftCampaigns = getSelectedGiftsCampaigns(giftCampaigns || []);
    const selectedGiftsNoncombinable = getSelectedGiftsNoncombinable(noncombinableGiftCampaigns || []);
    const selectedGiftsLevel = getSelectedGiftsLevelCampaigns(levelableGiftCampaigns || []);
    const all = [...selectedGiftCampaigns, ...selectedGiftsNoncombinable, ...selectedGiftsLevel];
    acc.push(...all);
    return acc;
  }, [] as SelectedGift[]);
}

export function isImportableSameOrder(campaigns: GiftCampaign[] = []) {
  return campaigns.every(cam => !(cam.type === 'Type B' && !cam.importableSameOrder));
}
