import { Col, Row } from 'antd';
import React, { ReactNode } from 'react';

import style from './NotificationItem.module.less';

interface NotificationItemProps {
  title: string;
  content: string | ReactNode;
  createdAt: string;
  read: boolean;
  icon?: React.ElementType;
}

const NotificationItem = ({ icon: Icon = 'div', title, content, createdAt, read }: NotificationItemProps) => {
  return (
    <Row align="middle" justify="center">
      <Col span={4}>
        <Icon className={style.orderStatusIcon} />
      </Col>
      <Col span={18}>
        <p className={style.title}>{title}</p>
        <section className={style.content}>{content}</section>
        <p className={style.datetime}>{createdAt}</p>
      </Col>
      <Col span={2}>{!read && <div className={style.dot} />}</Col>
    </Row>
  );
};

export default NotificationItem;
