import React from 'react';
import { ThunderboltTwoTone } from '@ant-design/icons';

import { DiscountDetails } from '@hooks/variant';
import { Maybe } from '@graphql/types.generated';
import { toVNDShort } from '@utils/formatter';

import style from './styles/FirstOrderDiscountSuggestion.module.less';

interface FirstOrderDiscountSuggestion {
  firstOrderDiscount: Maybe<DiscountDetails>;
  currQuantity: number;
}

const FirstOrderDiscountSuggestion = ({ firstOrderDiscount, currQuantity }: FirstOrderDiscountSuggestion) => {
  if (!firstOrderDiscount) return null;

  const { percent, flatAmount, requirements } = firstOrderDiscount;
  const isAchieved = requirements.quantity_threshold <= currQuantity && requirements.max_quantity >= currQuantity;
  const isMinMaxEqual = requirements.quantity_threshold === requirements.max_quantity;

  return (
    <div className={style.root}>
      <p>
        <span className={style.discountIcon}>
          <ThunderboltTwoTone twoToneColor="#FEBA43" />
        </span>
        <span className={isAchieved ? style.discountAchieved : style.discountNotAchieved}>
          {isAchieved ? ' t' : 'Cha t'}
        </span>
        <span className={style.discountText}>
          Mua {!isMinMaxEqual && 't '}
          <span className={style.highlight}>
            {requirements.quantity_threshold} {!isMinMaxEqual && `~ ${requirements.max_quantity}`} ci
          </span>{' '}
           t chit khu
          <span className={style.highlight}>{percent ? ` ${percent}% ` : ` ${toVNDShort(flatAmount || 0)} `}</span>
          cho n hng u tin
        </span>
        <span className={style.note}> (khng p dng cng lc vi cc chng trnh qu tng khc)</span>
      </p>
    </div>
  );
};

export default FirstOrderDiscountSuggestion;
