import { Maybe } from '@graphql/types.generated';
import { Variants } from '@components/SearchBar/SearchResult';
import { Variant } from '@graphql/types.generated';
import { DiscountDetails } from '@hooks/variant';

export function accumulateVariantValue(optionValuesArray: { value: string; id: string }[]) {
  return (
    optionValuesArray?.reduce((acc, item, index) => {
      acc += item ? item.value : '';
      if (index < optionValuesArray.length - 1) acc += ', ';

      return acc;
    }, '') || ''
  );
}

export const mapVariantToProduct = (variants: Maybe<Variant[]>) => {
  return variants?.map(variant => ({
    id: variant.product.id,
    name: variant.name,
    defaultVariant: {
      id: variant.id,
      available: variant.available || false,
      name: variant.name || '',
      brand: variant.product.brand?.name || '',
      materialNumber: variant.materialNumber || '',
      defaultImageUrl: variant.defaultImageUrl || '',
      campaignIds: variant.campaignIds || [],
      imageUrls: variant.imageUrls || '',
      vatWholesalePrice: variant.vatWholesalePrice || 0,
      netPrice: variant.netPrice || 0,
      vatPrice: variant.vatPrice || 0,
      discounts: variant.discounts || [],
      firstOrderDiscount: variant.firstOrderDiscount as DiscountDetails,
      optionValues: variant.optionValues || [],
    },
  }));
};

export function toBottomUnavailVariants(variants: Variants): Variants {
  if (!variants || variants.length === 0) return [];

  const availableArr: Variants = [];
  const unavailableArr: Variants = [];

  variants.forEach(variant => {
    if (variant) {
      const { available } = variant;

      available ? availableArr.push(variant) : unavailableArr.push(variant);
    }
  });

  return [...availableArr, ...unavailableArr] as Variants;
}
