import React from 'react';
import {
  AppstoreFilled,
  ThunderboltFilled,
  BulbFilled,
  ToolFilled,
  ControlFilled,
  HomeFilled,
  TabletFilled,
  BugFilled,
  FilterFilled,
  DatabaseFilled,
  GoldenFilled,
  LikeFilled,
} from '@ant-design/icons';

export const iconMap: Record<string, React.ElementType> = {
  '101': AppstoreFilled,
  '123': BulbFilled,
  '150': ToolFilled,
  '185': ControlFilled,
  '189': HomeFilled,
  '197': ThunderboltFilled,
  '214': TabletFilled,
  '215': BugFilled,
  '216': FilterFilled,
  '217': DatabaseFilled,
  '218': GoldenFilled,
  '219': LikeFilled,
};

export const _Icon = () => null;

export function getCategoryIconById(id: string) {
  return iconMap[id] || _Icon;
}
