import React, { memo } from 'react';
import { Badge, Button, Dropdown, Empty, Menu, message } from 'antd';
import { BellOutlined, CheckOutlined } from '@ant-design/icons';
import router from 'next/router';
import dayjs from 'dayjs';

import useNotification from './useNotification';

import NotificationFilter from './NotificationFilter';
import NotificationItem from './NotificationItem';
import InclinedBell from '@icons/InclinedBell';
import NotificationListSkeleton from '@components/Skeleton/NotificationListSkeleton';

import style from './NotificationCenter.module.less';

interface NotificationCenterProps {
  isMobile?: boolean;
}

const NotificationCenter = ({ isMobile = false }: NotificationCenterProps) => {
  const {
    filterValue,
    handleFilterChange,
    unreadNotificationsCount,
    userNotifications,
    isLoading,
    isError,
    handleSeeDetails,
    handleMarkAllNotificationsAsRead,
  } = useNotification();

  const isNotificationEmpty = userNotifications.length === 0;
  const userNotificationsContent = userNotifications.slice(0, 5).map(notification => (
    <Menu.Item
      key={notification.id}
      className={notification.read ? '' : style.unread}
      onClick={() => handleSeeDetails(notification)}
    >
      <NotificationItem
        icon={notification.icon as React.ElementType}
        title={notification.title || ''}
        content={notification.content}
        createdAt={dayjs(notification.createdAt).format('DD/MM/YYYY - HH:mm')}
        read={notification.read}
      />
    </Menu.Item>
  ));

  const renderNotifications = () => {
    if (isLoading) return <NotificationListSkeleton />;
    if (isNotificationEmpty) return <Empty description="Bn khng c thng bo no" image={<InclinedBell />} />;
    return userNotificationsContent;
  };

  const handleSeeAllNotifications = () => {
    router.push('/account/notifications');
  };

  const notificationsOverlay = (
    <Menu>
      <Menu.Item key="header">
        <div className={style.header}>
          <p className={style.title}>Thng Bo Mi</p>
          <Button type="text" onClick={handleMarkAllNotificationsAsRead}>
            <CheckOutlined /> nh du tt c  c
          </Button>
        </div>
      </Menu.Item>
      <NotificationFilter filterValue={filterValue} onFilterChange={handleFilterChange} />
      {renderNotifications()}
      <Menu.Item key="action" onClick={handleSeeAllNotifications}>
        <div className={style.seeAll}>{!isNotificationEmpty && <Button type="text">Xem tt c</Button>}</div>
      </Menu.Item>
    </Menu>
  );

  if (isError) {
    message.error(' c li xy ra, vui lng th li sau!');
    return null;
  }

  if (isMobile)
    return (
      <div onClick={() => router.push('/account/notifications')}>
        <Badge count={unreadNotificationsCount} size="small">
          <BellOutlined className={style.bellIcon} />
        </Badge>
      </div>
    );

  return (
    <div className={style.root}>
      <Dropdown
        overlay={notificationsOverlay}
        trigger={['click']}
        overlayClassName={style.overlay}
        placement="bottomRight"
      >
        <Badge count={unreadNotificationsCount} offset={[-5, 5]}>
          <BellOutlined className={style.bellIcon} />
        </Badge>
      </Dropdown>
    </div>
  );
};

export default memo(NotificationCenter);
