import APIEndpoints from './APIEndpoints';
import { tryParse } from './formatter';
import { isBrowser } from './isBrowser';

const checkStatus = (response: Response) => {
  if (response.status >= 200 && response.status < 300) {
    return response;
  }
  // TODO: Improve handle error
  const error = new Error('Something wrong!');
  throw error;
};

export function request(endpoint: string, options: RequestInit = {}) {
  const defaultOptions = {
    Accept: 'application/json',
  };
  const newOptions: RequestInit = { ...defaultOptions, ...options };
  if (newOptions.method === 'POST' || newOptions.method === 'PUT' || newOptions.method === 'DELETE') {
    if (!(newOptions.body instanceof FormData)) {
      newOptions.headers = {
        'Content-Type': 'application/json; charset=utf-8',
        ...newOptions.headers,
      };
      newOptions.body = JSON.stringify(newOptions.body);
    }
  }

  const auth = isBrowser ? localStorage.getItem('auth') : null;
  if (auth) {
    const authObj = tryParse(auth);
    newOptions.headers = {
      ...newOptions.headers,
      authorization: `Bearer ${authObj?.token}`,
    };
  }

  const url = APIEndpoints.REST_URL + endpoint;

  return fetch(url, newOptions)
    .then(checkStatus)
    .then(response => {
      // DELETE and 204 do not return data by default
      // using .json will report an error.
      if (newOptions.method === 'DELETE' || response.status === 204) {
        return response.text();
      }
      return response.json();
    });
}
