import React, { useEffect, useMemo, createContext, useContext, useState, useCallback } from 'react';
import { ApolloError, getApolloContext, ApolloCache, FetchResult, MutationFunctionOptions } from '@apollo/client';
import { message } from 'antd';
import omit from 'lodash/omit';
import get from 'lodash/get';

import {
  useGetCartQuery,
  useGetOrderItemsWithQuantityLazyQuery,
  GetOrderItemsWithQuantityQuery,
  GetCartQuery,
  useUpdateCartMutation,
  OrderItem,
  UpdateCartMutation,
  UpdateCartInput,
  GetCartDocument,
  Exact,
  Maybe,
} from './cart.gql.generated';
import { OrderItemConnectFragment } from '@hooks/cart';
import { useGetOrderItemLazyQuery, GetOrderItemQuery } from '@hooks/orderItem';
import { PERFORMANCE_ITEM_PER_PAGE } from '@utils/constants';

export type UpdateCartOptions = MutationFunctionOptions<
  UpdateCartMutation,
  Exact<{
    input: UpdateCartInput;
    first?: Maybe<number>;
    after?: Maybe<string>;
  }>
>;

type CartContextType = {
  updateCount: number;
  setUpdateCount?: (counter: number) => void;
  data?: GetCartQuery;
  orderItemData?: GetOrderItemQuery;
  loading: boolean;
  initialLoading: boolean;
  updateCartLoading?: boolean;
  error?: ApolloError;
  variantIdWithCartData: VariantIdWithCartData;
  isMiniCartItemDetailVisible: boolean;
  selectedCartItemId: string | undefined;
  selectedVariantId?: string;
  fetchMoreCartItem?: (endCursor: string) => void;
  handleOpenMiniCartDetail?: (id: string, variantId?: string) => void;
  handleCloseMiniCartDetail?: () => void;
  handleUpdateCart: (variantId: string, quantity: number, options?: UpdateCartOptions) => void;
  orderItemLoading?: boolean;
  refetchAfterUpdatingCart: boolean;
  setRefetchAfterUpdatingCart?: React.Dispatch<React.SetStateAction<boolean>>;
};

export type VariantIdWithCartData = {
  [key: number]: {
    quantity: number;
    orderItemId: string;
    vatWholesalePrice: number;
  };
};

type CartControllerProps = {
  children: React.ReactNode;
};

const CartContext = createContext<CartContextType>({
  updateCount: 0,
  loading: false,
  initialLoading: false,
  isMiniCartItemDetailVisible: false,
  selectedCartItemId: '',
  selectedVariantId: undefined,
  variantIdWithCartData: {},
  handleUpdateCart: () => null,
  refetchAfterUpdatingCart: false,
});

function transformVariantByOrder(data: GetCartQuery | GetOrderItemsWithQuantityQuery | undefined) {
  const orderItems: OrderItem[] = get(data, 'cart.orderItems.nodes', []);
  return orderItems.reduce((acc, orderItem) => {
    const id: string = orderItem.variant.id as string;
    if (id)
      acc[id] = {
        orderItemId: orderItem.id,
        quantity: orderItem.quantity,
        vatWholesalePrice: Number(orderItem.vatWholesalePrice),
      };

    return acc;
  }, {} as Record<string, any>);
}

// store order in variantIdWithCartData as key value pair for easy access

export function CartController({ children }: CartControllerProps) {
  const [updateCount, setUpdateCount] = useState(0);
  const [variantIdWithCartData, setVariantIdWithCartData] = useState({});
  const [isMiniCartItemDetailVisible, setMiniCartItemDetailVisibility] = useState(false);
  const [selectedCartItemId, setSelectedCartItemId] = useState<string | undefined>(undefined);
  const [selectedVariantId, setSelectedVariantId] = useState<string | undefined>(undefined);
  const [removingVariantId, setRemovingVariantId] = useState<string>('');

  // get every order item because cart is paginated and all items's quantity are needed for instantdeal page
  const [fetchAllOrderItems] = useGetOrderItemsWithQuantityLazyQuery({
    fetchPolicy: 'no-cache',
    onCompleted: response => {
      const transformedData = transformVariantByOrder(response);
      setVariantIdWithCartData(transformedData);
    },
  });
  const { client } = useContext(getApolloContext());
  const [refetchAfterUpdatingCart, setRefetchAfterUpdatingCart] = useState(false);

  const {
    data,
    loading,
    error,
    fetchMore,
    refetch: refetchCart,
  } = useGetCartQuery({
    variables: { first: PERFORMANCE_ITEM_PER_PAGE },
    notifyOnNetworkStatusChange: true,
  });

  const [fetchOrderItem, { data: orderItemData, loading: orderItemLoading }] = useGetOrderItemLazyQuery();
  const [updateCart, { loading: updateCartLoading }] = useUpdateCartMutation();
  const initialLoading = loading && !data && !error;

  const handleUpdateCart = React.useCallback(
    async (variantId: string, quantity: number, options?: UpdateCartOptions) => {
      const currOrderItemId = quantity === 0 ? get(variantIdWithCartData, `${variantId}.orderId`, '') : '';
      try {
        await updateCart({
          variables: {
            input: { variantId, quantity },
            first: PERFORMANCE_ITEM_PER_PAGE,
          },
          update: (
            cache: ApolloCache<UpdateCartMutation>,
            mutationResult: FetchResult<UpdateCartMutation, Record<string, any>, Record<string, any>>,
          ): void => {
            if (!mutationResult.data?.updateCart) return;

            fetchAllOrderItems();
            currOrderItemId && cache.evict({ id: `OrderItem:${currOrderItemId}` });
            cache.gc(); // remove stale order item from Apollo InMemoryCache
          },
          ...options,
        });

        setRemovingVariantId(variantId);
        setUpdateCount(prev => prev + 1);
        setRefetchAfterUpdatingCart(true);
      } catch (error) {
        message.error((error as Error).message);
      }
    },
    [variantIdWithCartData],
  );

  const handleOpenMiniCartDetail = React.useCallback((id: string, variantId?: string) => {
    setSelectedCartItemId(id);
    setSelectedVariantId(variantId);
    setMiniCartItemDetailVisibility(true);
  }, []);

  const handleCloseMiniCartDetail = React.useCallback(() => {
    setSelectedCartItemId(undefined);
    setSelectedVariantId(undefined);
    setMiniCartItemDetailVisibility(false);
  }, []);

  const fetchMoreCartItem = React.useCallback(
    endCursor => {
      fetchMore?.({
        variables: {
          first: PERFORMANCE_ITEM_PER_PAGE,
          after: endCursor,
        },
        updateQuery: (
          prev: GetCartQuery,
          {
            fetchMoreResult,
          }: {
            fetchMoreResult?: GetCartQuery;
          },
        ): GetCartQuery => {
          const orderItems = get(fetchMoreResult, 'cart.orderItems.nodes', []);
          if (!orderItems.length) return prev;

          const previousOrderItems = get(prev, 'cart.orderItems.nodes', []);
          const orderItemFragment = get(fetchMoreResult, 'cart.orderItems', {}) as OrderItemConnectFragment;

          return {
            ...prev,
            cart: {
              ...prev.cart,
              orderItems: {
                ...orderItemFragment,
                nodes: [...previousOrderItems, ...orderItems],
              },
            },
          };
        },
      });
    },
    [fetchMore],
  );

  useEffect(() => {
    refetchCart({
      first: PERFORMANCE_ITEM_PER_PAGE,
    });
    fetchAllOrderItems();
  }, []);

  useEffect(() => {
    const orderItemId = get(variantIdWithCartData, `${Number(selectedVariantId || 0)}.orderId`, null);
    if (orderItemId) {
      setSelectedCartItemId(orderItemId);
      fetchOrderItem({
        variables: {
          id: orderItemId,
        },
      });
    }

    const data = client?.cache.readQuery({ query: GetCartDocument, variables: { first: PERFORMANCE_ITEM_PER_PAGE } });
    if (!get(variantIdWithCartData, `${removingVariantId}`)) {
      const firstItem = get(data, 'cart.orderItems.nodes[0]') as OrderItem;

      // adding new cart item would cause variantIdWithCartData outdated
      if (!get(variantIdWithCartData, `${firstItem?.variant?.id}`, '')) {
        const variantId = get(firstItem, 'variant.id', '');
        variantId &&
          setVariantIdWithCartData(prev => ({
            ...prev,
            [variantId]: {
              orderId: firstItem.id,
              quantity: firstItem.quantity,
              vatWholesalePrice: Number(firstItem.vatWholesalePrice),
            },
          }));
      }
    } else {
      const newVariantIdCartData = omit(variantIdWithCartData, [removingVariantId]);
      setVariantIdWithCartData(newVariantIdCartData);
    }
    setRemovingVariantId('');
  }, [updateCount]);

  const context = useMemo(
    () => ({
      data,
      updateCount,
      setUpdateCount,
      orderItemData,
      initialLoading,
      loading,
      error,
      variantIdWithCartData,
      fetchMoreCartItem,
      handleUpdateCart,
      handleOpenMiniCartDetail,
      handleCloseMiniCartDetail,
      isMiniCartItemDetailVisible,
      selectedCartItemId,
      selectedVariantId,
      orderItemLoading,
      updateCartLoading,
      refetchAfterUpdatingCart,
      setRefetchAfterUpdatingCart,
    }),
    [
      loading,
      initialLoading,
      data,
      updateCount,
      orderItemData,
      variantIdWithCartData,
      handleUpdateCart,
      isMiniCartItemDetailVisible,
      selectedCartItemId,
      selectedVariantId,
      orderItemLoading,
      updateCartLoading,
      refetchAfterUpdatingCart,
    ],
  );

  return <CartContext.Provider value={context}>{children}</CartContext.Provider>;
}

export function useCartData() {
  return useContext(CartContext);
}

export function useFetchMoreCartItems() {
  const { data, loading, fetchMoreCartItem } = useCartData();
  const totalCount = get(data, 'cart.orderItems.totalCount');
  const products = get(data, 'cart.orderItems.nodes', []);
  const { hasNextPage, endCursor } = get(data, 'cart.orderItems.pageInfo', { hasNextPage: false, endCursor: '' });

  const fetchMoreProducts = useCallback(() => {
    fetchMoreCartItem?.(endCursor);
  }, [endCursor]);

  useEffect(() => {
    if (hasNextPage && products.length < totalCount && products.length % 10 !== 0) {
      fetchMoreProducts();
    }
  }, [products.length]);

  return { loading, hasNextPage, endCursor, fetchMoreProducts };
}

export const CartConsumer = CartContext.Consumer;
