import { Skeleton } from 'antd';
import React from 'react';

import style from './NotificationListSkeleton.module.less';

interface NotificationListSkeletonProps {
  titleLength?: number;
  contentLength?: number;
}

const NotificationListSkeleton = ({ titleLength = 200, contentLength = 270 }: NotificationListSkeletonProps) => {
  const notifications = new Array(5).fill(0).map((_, idx) => (
    <div className={style.notification} key={idx}>
      <Skeleton.Avatar active size="large" shape="circle" />
      <Skeleton paragraph={{ rows: 1, width: [contentLength] }} title={{ width: titleLength }} />
    </div>
  ));

  return <div className={style.root}>{notifications}</div>;
};

export default NotificationListSkeleton;
