import React, { useEffect, useState, useRef } from 'react';
import { Menu, Button, Dropdown, Spin } from 'antd';
import { MenuOutlined, CaretRightFilled } from '@ant-design/icons';
import Link from 'next/link';

import { GetCategoriesQuery, useGetCategoriesLazyQuery, getCategoryIconById } from '@hooks/category';
import useWindowSize, { Size } from '@hooks/useWindowSize';
import { toBreakpointNumber, MD, XL } from '@utils/mediaQuery';
import { MAX_MENU_WIDTH, MENU_PADDING, MIN_MENU_WIDTH } from './utils';

import s from './index.module.less';

type CategoryItem = GetCategoriesQuery['categories'][0];

type HoverItem = {
  key: React.Key;
  hover: boolean;
};

export type HoverEventHandler = (info: HoverItem) => void;

type MenuOverlayItem = {
  category: CategoryItem;
  onHover: (info: HoverItem, category: CategoryItem) => void;
  onItemHover?: HoverEventHandler;
};

const MenuOverlayItem = ({ category, onHover, onItemHover: _onItemHover, ...props }: MenuOverlayItem) => {
  const Icon = getCategoryIconById(category.id);
  const hasChildren = category.children && category.children.length > 0;

  const onItemHover = (event: { key: React.Key }) => {
    const info = {
      key: event.key,
      hover: true,
    };
    onHover(info, category);
    _onItemHover?.(info);
  };

  return (
    <Menu.Item className={s.menuItem} onMouseEnter={onItemHover} icon={<Icon className={s.menuIcon} />} {...props}>
      <Link href={`/categories/${category.id}`} passHref>
        <a>
          <span className={s.menuItemTitle}>{category.name}</span>
          <span
            role="img"
            aria-label="caret-right"
            className="anticon anticon-caret-right ant-menu-submenu-expand-icon"
          >
            {hasChildren && <CaretRightFilled />}
          </span>
        </a>
      </Link>
    </Menu.Item>
  );
};

type MenuOverlayProps = { visible: boolean; buttonRef: React.RefObject<HTMLButtonElement> };

const MenuOverlay = ({ visible, buttonRef }: MenuOverlayProps) => {
  const [fetchCategories, { loading, data }] = useGetCategoriesLazyQuery();
  const size: Size = useWindowSize();

  // Filter out, "Smart devices" with id 185
  const filteredCategories = data?.categories.filter(cat => cat.id !== '185');

  const [hoveredCategory, setHoveredCategory] = useState<CategoryItem | null>(null);

  const handleHover = ({ hover }: HoverItem, category: CategoryItem) => {
    if (hover) {
      setHoveredCategory(category);
    }
  };

  const getMode = () => {
    if (!size.width || size.width <= toBreakpointNumber(MD)) return 'horizontal';
    return 'vertical';
  };

  const getRootStyles = () => {
    if (!hoveredCategory) return {};
    const width =
      size.width && size.width >= toBreakpointNumber(XL) ? toBreakpointNumber(XL) : (size.width || 0) - MENU_PADDING;
    return { width };
  };

  const getMenuStyles = () => {
    if (!buttonRef.current) return { width: MAX_MENU_WIDTH };

    let width = buttonRef.current.offsetWidth;
    if (buttonRef.current.offsetWidth <= MIN_MENU_WIDTH) {
      width = (size.width || 0) - MENU_PADDING;
    }
    return { width };
  };

  useEffect(() => {
    fetchCategories();
  }, []);

  useEffect(() => {
    if (!visible) {
      setHoveredCategory(null);
    }
  }, [visible]);

  const rootStyles = getRootStyles();
  const menuStyles = getMenuStyles();
  const mode = getMode();

  return (
    <div className={s.root} style={rootStyles}>
      <Menu className={s.menuRoot} style={menuStyles} mode={mode}>
        {loading ? (
          <div className={s.loading}>
            <Spin />
          </div>
        ) : (
          filteredCategories?.map(category => (
            <MenuOverlayItem onHover={handleHover} key={category.id} category={category} />
          ))
        )}
      </Menu>
      {hoveredCategory && (
        <div
          className={s.subMenuRoot}
          onMouseOver={() => handleHover({ hover: true, key: hoveredCategory.id }, hoveredCategory)}
        >
          <div className={s.subMenuLink}>
            {hoveredCategory.children?.map(subCategory => (
              <div className={s.categoryItem} key={subCategory.id}>
                <Link href={`/categories/${subCategory.id}`} passHref>
                  <a>
                    <p className={s.categoryItemTitle}>{subCategory.name}</p>
                  </a>
                </Link>
                <div className={s.categoryItemType}>
                  {subCategory.children?.map(_subCategory => (
                    <Link href={`/categories/${_subCategory.id}`} passHref key={_subCategory.id}>
                      <a>
                        <span className={s.subCategoryName}>{_subCategory.name}</span>
                      </a>
                    </Link>
                  ))}
                </div>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
};

const CategoryMenu = () => {
  const [visible, setVisible] = useState(false);
  const buttonRef = useRef(null);

  return (
    <Dropdown
      overlay={<MenuOverlay visible={visible} buttonRef={buttonRef} />}
      onVisibleChange={setVisible}
      trigger={['hover', 'click']}
    >
      <Button ref={buttonRef} block size="large" type="primary" icon={<MenuOutlined />} className={s.categoryButton}>
        Danh mc
      </Button>
    </Dropdown>
  );
};

export default CategoryMenu;
