import React from 'react';
import { Button, Badge, Tooltip } from 'antd';
import { TagOutlined } from '@ant-design/icons';
import { useRouter } from 'next/router';
import classnames from 'classnames';
import Link from 'next/link';
import { useAuth } from '@context/AuthContext';

import style from './index.module.less';

function GiftsBtnNavigation() {
  const { isLoggedIn } = useAuth();
  const router = useRouter();

  // TODO: count valule in Badge should be the number of qualified giftCampaigns
  // we'll need socket for this one
  return isLoggedIn && !router.pathname.includes('account/campaign') ? (
    <section className={classnames([style.container, router.pathname.includes('/cart') && style.containerCartRoute])}>
      <Tooltip title=" iu kin t qu tng mi" placement="topRight">
        <Badge count={null} className={style.badge}>
          <Link href="/account/campaign" passHref>
            <Button icon={<TagOutlined />} shape="circle" type="primary" size="large" />
          </Link>
        </Badge>
      </Tooltip>
    </section>
  ) : null;
}

export default GiftsBtnNavigation;
