import React from 'react';
import { Menu, Dropdown } from 'antd';
import router from 'next/router';
import Image from 'next/image';
import { Key } from 'antd/lib/table/interface';
import { DownOutlined } from '@ant-design/icons';

import { Campaigns } from '@hooks/giftCampaigns/controller';
import { toVNDShort } from '@utils/formatter';
import { ElementType } from '@utils/typeUtils';
import { calculateMultiplyBy } from './utils';

import style from './styles/PromotionSelection.module.less';

interface PromotionSelectionProps {
  variantGiftCampaigns: Campaigns;
  options?: {
    forVariants?: boolean;
    itemWholesalePrice?: number;
    textOnly?: boolean;
    isPromotionTextHidden?: boolean;
  };
  disabled?: boolean;
}

type Target = {
  amountToTarget: string;
  multiplyBy: number;
  promotionName: string;
} | null;

// This function is used to insert spaces to group of variants' name
// to make it display properly, see this issue for more information:
// https://github.com/nanocohub/xshop/issues/1944
const insertSpaceToPromotionName = (promotionName = ''): string => {
  return promotionName.replace(/\//g, ' / ');
};

const sortPromotionsByRequirement = (promotions: Campaigns) => {
  const promotionWithFirstRequirement = promotions.map(promo => {
    return {
      ...promo,
      totalQuantityThreshold: promo.requirements[0].totalQuantityThreshold,
      totalRevenueThreshold: promo.requirements[0].totalRevenueThreshold,
      totalGiftConditionItemsQuantityThreshold: promo.requirements[0].totalGiftConditionItemsQuantityThreshold,
    };
  });

  promotionWithFirstRequirement
    .sort((promotionOne, promotionTwo) =>
      Number(BigInt(promotionOne.totalRevenueThreshold || 0n) - BigInt(promotionTwo.totalRevenueThreshold || 0n)),
    )
    .sort(
      (promotionOne, promotionTwo) =>
        (promotionOne.totalQuantityThreshold || 0) - (promotionTwo.totalQuantityThreshold || 0),
    )
    .sort(
      (promotionOne, promotionTwo) =>
        (promotionOne.totalGiftConditionItemsQuantityThreshold || 0) -
        (promotionTwo.totalGiftConditionItemsQuantityThreshold || 0),
    );

  return promotionWithFirstRequirement;
};

const calculateAmountToTarget = (promotion: ElementType<Campaigns>) => {
  const { totalQuantityThreshold, totalRevenueThreshold, totalGiftConditionItemsQuantityThreshold } =
    promotion.requirements[0];

  // When quantity of each condition item to buy is unknown
  // Can buy 1A/2B/5C as long as total quantity is equal to totalQuantityThreshold
  if (totalQuantityThreshold) {
    const inProgressQuantity =
      promotion?.progressData?.map(item => item.quantity).reduce((acc, val) => acc + val, 0) || 0;
    const quantityToTarget = totalQuantityThreshold - (inProgressQuantity % totalQuantityThreshold);

    return quantityToTarget.toString() + ' ci';
  }

  // When revenue of each condition item to buy is unknown
  // Can buy 5tr A/2tr B/5tr C as long as total revenue is equal to totalRevenueThreshold
  if (totalRevenueThreshold) {
    const inProgressRevenue =
      promotion?.progressData?.map(item => BigInt(item.revenue)).reduce((acc, val) => acc + val, 0n) || 0n;
    const revenueToTarget = BigInt(totalRevenueThreshold) - (inProgressRevenue % BigInt(totalRevenueThreshold));

    return toVNDShort(revenueToTarget);
  }

  // When quantity of each condition item to buy is known
  // Have to buy exactly 1A/2B/5C
  if (totalGiftConditionItemsQuantityThreshold && totalGiftConditionItemsQuantityThreshold !== 0) {
    const inProgressQuantity =
      promotion?.progressData?.map(item => item.quantity).reduce((acc, val) => acc + val, 0) || 0;
    const quantityToTarget =
      totalGiftConditionItemsQuantityThreshold - (inProgressQuantity % totalGiftConditionItemsQuantityThreshold);

    return quantityToTarget.toString() + ' ci';
  }

  return '';
};

const PromotionSelection = ({ variantGiftCampaigns = [], options = {}, disabled = false }: PromotionSelectionProps) => {
  const promotions = variantGiftCampaigns.filter(campaign => !campaign.duplicatedFrom);
  // Put promotion which has smaller required amount to top
  const sortedPromotions = sortPromotionsByRequirement(promotions);

  const targets: Target[] = [
    // Using set to remove duplicated targets number for a promotion
    ...new Set(
      sortedPromotions.map(promotion => {
        const amountToTarget = calculateAmountToTarget(promotion);
        const multiplyBy = calculateMultiplyBy(promotion);
        return {
          amountToTarget: amountToTarget,
          multiplyBy,
          promotionName: promotion.campaignName,
        };
      }),
    ),
  ];
  const theFirstTarget = targets[0];

  const handleSelect = ({ key }: { key: Key }) => {
    router.push(`/instantdeal?gift_campaign_id=${key}`);
  };

  const menu = (
    <Menu onClick={handleSelect} className={style.menu}>
      {sortedPromotions?.map(promotion => (
        <Menu.Item key={promotion.id} className={style.menuItem}>
          <div className={style.promotionItem}>
            {calculateMultiplyBy(promotion) >= 1 ? (
              <p>
                <span className={style.promotionItemAchieved}> t ({calculateMultiplyBy(promotion)} ln)</span>
                {'  '}
                <span className={style.promotionItemSuggested}>
                  Mua thm {calculateAmountToTarget(promotion)}  t {calculateMultiplyBy(promotion) + 1} ln
                </span>
              </p>
            ) : (
              <p>
                <span className={style.promotionItemNotAchieved}>Cha t</span>
                {'  '}
                <span className={style.promotionItemSuggested}>
                  Mua thm {calculateAmountToTarget(promotion)}  t
                </span>
              </p>
            )}
            <p>{promotion.campaignName}</p>
          </div>
        </Menu.Item>
      ))}
    </Menu>
  );

  if (promotions?.length === 0) return null;

  return (
    <div className={style.root}>
      {!options?.isPromotionTextHidden && (
        <div className={disabled ? style.disabledText : style.promotionText}>
          <Image className={style.fireIcon} src="/images/fire.svg" alt="fire icon" width={13} height={14} />
          &nbsp; Mua thm <span className={style.highlight}>{theFirstTarget?.amountToTarget}</span>  t{' '}
          <span className={style.highlight}>{theFirstTarget ? theFirstTarget?.multiplyBy + 1 : ''} ln</span> qu tng -
          Chng trnh <span>{insertSpaceToPromotionName(theFirstTarget?.promotionName)}</span>
        </div>
      )}
      {!options.textOnly && (
        <Dropdown overlay={menu} trigger={['click']} placement="bottomCenter" disabled={disabled}>
          <a className="ant-dropdown-link" onClick={e => e.preventDefault()}>
            <div className={disabled ? style.disabledSeeMore : style.seeMore}>
              Xem thm&nbsp;
              <DownOutlined />
            </div>
          </a>
        </Dropdown>
      )}
    </div>
  );
};

export default PromotionSelection;
