import { FetchResult } from '@apollo/client';
import get from 'lodash/get';

import { ColumnFilterItem } from '@components/MultipleFilterDropdown';
import { CheckoutMutation, CheckoutInternalOrdersMutation, OrderItem } from '@hooks/checkout';

/**
 * This one contains all of custom events
 */

export const firePurchaseEvent = (
  mutationResult:
    | FetchResult<CheckoutMutation, Record<string, any>, Record<string, any>>
    | FetchResult<CheckoutInternalOrdersMutation, Record<string, any>, Record<string, any>>,
) => {
  const checkout = get(mutationResult, 'data.checkout');
  const checkoutWithInternalDispatch = get(mutationResult, 'data.checkoutWithInternalDispatch');
  const data = checkout || checkoutWithInternalDispatch;

  if (!data) return;

  window.dataLayer = window.dataLayer || [];
  window.dataLayer.push({ ecommerce: null });

  // TODO: remove when /checkout is removed
  if (!Array.isArray(data)) {
    window.dataLayer.push({
      event: 'purchase',
      ecommerce: {
        transaction_id: data?.id,
        value: Number(data?.total),
        currency: 'VND',
        items: data?.orderItems?.nodes?.map((item: OrderItem) => ({
          item_id: item?.variant.id,
          item_name: item?.variant.name,
          item_variant: item?.variant.materialNumber,
          price: Number(item?.price),
          quantity: item?.quantity,
        })),
      },
    });
  } else {
    for (const el of data) {
      window.dataLayer.push({
        event: 'purchase',
        ecommerce: {
          transaction_id: el?.id,
          value: Number(el?.total),
          currency: 'VND',
          items: el?.orderItems?.nodes?.map((item: OrderItem) => ({
            item_id: item?.variant.id,
            item_name: item?.variant.name,
            item_variant: item?.variant.materialNumber,
            price: Number(item?.price),
            quantity: item?.quantity,
          })),
        },
      });
    }
  }
};

