export const fireViewItemEvent = (item: Record<string, any>) => {
  const { id, name, materialNumber, vatWholesalePrice } = item;
  window.dataLayer = window.dataLayer || [];
  window.dataLayer.push({ ecommerce: null });
  window.dataLayer.push({
    event: 'view_item',
    ecommerce: {
      items: [
        {
          item_id: id,
          item_name: name,
          item_variant: materialNumber,
          currency: 'VND',
          price: vatWholesalePrice,
        },
      ],
    },
  });
};

