/**
 * This one contains all utility functions
 */
interface FilterItem {
  text: string;
  value: string;
}
export const getFilterValuesName = (
  filterValues: Record<string, string[]>,
  categories: ColumnFilterItem[],
  brands: FilterItem[],
  colors: FilterItem[],
  attributes: FilterItem[],
) => {
  const categoryNames: string[] = [];
  const brandNames: string[] = [];
  const colorNames: string[] = [];
  const attributeNames: string[] = [];

  const categoryFilterValues = filterValues.categoryIds;
  const brandFilterValues = filterValues.brandIds;
  const colorFilterValues = filterValues.optionValueIds;
  const attributeFilterValues = filterValues.attributes;

  const flatCategories: ColumnFilterItem[] = [];
  function flattenCategory(categories: ColumnFilterItem[]) {
    if (!categories) return;

    categories.forEach(category => {
      if (category.children) {
        flattenCategory(category.children);
      }
      flatCategories.push(category);
    });
  }
  flattenCategory(categories);

  flatCategories.forEach(category => {
    if (categoryFilterValues.includes(category.value as string)) categoryNames.push(category.text as string);
  });
  brands.forEach(brand => {
    if (brandFilterValues.includes(brand.value)) brandNames.push(brand.text);
  });
  colors.forEach(color => {
    if (colorFilterValues.includes(color.value)) colorNames.push(color.text);
  });
  attributes.forEach(attr => {
    if (attributeFilterValues.includes(attr.value)) attributeNames.push(attr.text);
  });

  return categoryNames.concat(brandNames).concat(colorNames).concat(attributeNames).join('-');
};
