import { ExclamationCircleOutlined } from '@ant-design/icons';
import { memo, useCallback, useEffect, useState } from 'react';
import { Select, Typography, Modal, message } from 'antd';
import dayjs from 'dayjs';
import cx from 'classnames';

import { useAuth } from '@context/AuthContext';
import { request } from '@utils/request';
import { isBrowser } from '@utils/isBrowser';
import useCurrentUser from '@hooks/useCurrentUser';
import usePersistedState from '@hooks/usePersistedState';

import s from './index.module.less';

const { Option } = Select;
const { Paragraph } = Typography;
const { confirm } = Modal;

interface Customer {
  id: string;
  name: string;
  user_to_login: number;
  latest_order_at?: string;
  addresses?: string[];
}

function SaleAdminBanner() {
  const { isLoggedIn } = useAuth();
  const { id: currentUserId } = useCurrentUser();
  const [bypassBy] = usePersistedState('bypassBy', null);

  const [customers, setCustomers] = useState<Customer[]>([]);
  const [selectedCustomer, setSelectedCustomer] = useState(currentUserId);
  const isAdminLoggin = bypassBy === 'admin';

  useEffect(() => {
    async function handleFetchCustomer() {
      try {
        const response = await request('/admin/managed_customers', {
          method: 'GET',
        });
        setCustomers(response);
        setSelectedCustomer(currentUserId);
      } catch (e) {
        console.error(e);
        setCustomers([]);
      }
    }
    if (isBrowser && isAdminLoggin) {
      handleFetchCustomer();
    }
  }, []);

  const showConfirm = useCallback(
    (newSelectedCustomer: string) => {
      const customer = customers.find(customer => String(customer.user_to_login) === newSelectedCustomer);

      confirm({
        title: 'Xc nhn',
        icon: <ExclamationCircleOutlined />,
        content: `Bn s chuyn sang ti khon ca ${customer?.name}`,
        cancelText: 'Hu',
        okText: 'ng ',
        async onOk() {
          try {
            if (isBrowser && customer) {
              window.location.href = `/admin/login_as_user?user_id=${customer.user_to_login}`;
            }
          } catch (e: any) {
            message.error((e as Error).message);
          }
        },
        onCancel() {
          setSelectedCustomer(currentUserId);
        },
      });
    },
    [selectedCustomer, customers],
  );

  const handleSelectCustomer = useCallback(
    (newSelectedCustomer: string) => {
      setSelectedCustomer(newSelectedCustomer);
      showConfirm(newSelectedCustomer);
    },
    [showConfirm],
  );

  if (!isLoggedIn || !isAdminLoggin || customers.length === 0) return null;

  const currentCustomer = customers.find(customer => String(customer.user_to_login) === selectedCustomer);

  return (
    <div className={s.root}>
      {currentCustomer && (
        <>
          <div className={s.title}>Bn ang ng nhp bng ti khon ca khch hng: </div>
          <div className={s.companyName}>{currentCustomer.name}</div>
          <div className={s.companyInfo}>
            <div>M khch hng: {currentCustomer.id}</div>
            <div>a ch giao hng: {(currentCustomer.addresses || [''])[0]}</div>
            {currentCustomer.latest_order_at && (
              <div className={s.lastOrder}>
                Ln t n hng gn nht: {dayjs(currentCustomer.latest_order_at).format('DD/MM/YYYY - HH:mm')}{' '}
              </div>
            )}
          </div>
        </>
      )}
      <div className={s.space}>
        <div className={s.selectLabel}>Chn ti khon  ng nhp:</div>
        <div>
          <Select
            placeholder="Khch hng"
            className={s.select}
            size="large"
            value={selectedCustomer ? String(selectedCustomer) : selectedCustomer}
            onChange={handleSelectCustomer}
          >
            {customers.map(customer => (
              <Option
                value={String(customer.user_to_login)}
                key={customer.id}
                disabled={!customer.user_to_login}
                className={cx(!customer.user_to_login && s.optionDisabled)}
              >
                <Paragraph className={s.customer}>
                  <span className={s.customerName}>{customer.name}</span>
                  <br />
                  <span className={s.customerCode}>M khch hng: {customer.id}</span>
                </Paragraph>
              </Option>
            ))}
          </Select>
        </div>
      </div>
    </div>
  );
}

export default memo(SaleAdminBanner);
