import React, { useCallback } from 'react';
import merge from 'deepmerge';
import { message } from 'antd';

import { GetCategoryQuery, useGetCategoryLazyQuery, useGetCategoryWithGiftCampaignsLazyQuery } from '@hooks/category';

type Params = {
  after?: string;
  first?: number;
  optionValueIds?: string[];
  brandIds?: string[];
  categoryIds?: string[];
  attributes?: string[];
};

export type ProductCategoriesContextType = {
  productCategory?: GetCategoryQuery['category'];
  loading?: boolean;
  categoryDepth?: number;
  handleFetchMoreProducts?: (params: Params) => void;
  handleFilterProducts?: (params: Params) => void;
  fetchMoreLoading?: boolean;
};

const ProductCategoriesContext = React.createContext<ProductCategoriesContextType | undefined>(undefined);
export const useProductCategory = () => {
  const value = React.useContext(ProductCategoriesContext);
  if (!value) return {};
  return value;
};

type Props = {
  children?: React.ReactNode;
  initialParams?: Params;
  isGift?: boolean;
  cid?: string | string[];
};

function getCategoryDepth(category?: any, depth = 0): number {
  if (!category) return depth;
  if (category && !category.parent) depth;
  const nextDepth = depth + 1;
  return getCategoryDepth(category.parent, nextDepth);
}

export const ProductCategoryController = ({ cid = '', initialParams = {}, isGift = false, children }: Props) => {
  // Performance issue: Get list product with gift campaign is expensive.
  // So, we need to limit call this query and wait BE fix issue about performance
  const useGetCategory = isGift ? useGetCategoryWithGiftCampaignsLazyQuery : useGetCategoryLazyQuery;
  const [fetchCategory, { loading, data, error, fetchMore }] = useGetCategory({
    notifyOnNetworkStatusChange: true, // this is a must so fetchMore would have loading change https://github.com/apollographql/apollo-client/issues/6354
  });

  React.useEffect(() => {
    if (typeof cid === 'string' && cid) {
      try {
        fetchCategory({
          variables: {
            id: cid,
            first: 10,
            ...initialParams,
          },
        });
      } catch (error) {
        message.error(error.message);
      }
    }
  }, [cid]);

  const handleFetchMoreProducts = useCallback(
    (params: Params = {}) => {
      try {
        // @ts-ignore
        fetchMore?.({
          variables: {
            id: cid,
            first: 20,
            ...params,
          },
          updateQuery: (
            prev: GetCategoryQuery,
            {
              fetchMoreResult,
            }: {
              fetchMoreResult?: GetCategoryQuery;
            },
          ): GetCategoryQuery => {
            if (!fetchMoreResult?.category) {
              return prev;
            }
            return {
              ...prev,
              category: {
                ...merge(prev.category, fetchMoreResult.category),
                // deepmerge merge all children by adding previous children
                // so we need to overwrite children by new result to avoid duplicate
                children: fetchMoreResult.category.children,
              },
            };
          },
        });
      } catch (error) {
        message.error(error.message);
      }
    },
    [cid, fetchMore],
  );

  const handleFilterProducts = useCallback(
    (params: Params) => {
      try {
        fetchCategory({
          variables: {
            id: cid as string,
            first: 10,
            ...params,
          },
        });
      } catch (error) {
        message.error(error.message);
      }
    },
    [cid, fetchCategory],
  );

  const context = React.useMemo(() => {
    // first render loading
    const loadingContainer = loading && !data && !error;
    return {
      productCategory: data?.category,
      categoryDepth: getCategoryDepth(data?.category),
      loading: loadingContainer,
      fetchMoreLoading: loading,
      handleFetchMoreProducts,
      handleFilterProducts,
    };
  }, [loading, data, error, handleFetchMoreProducts]);

  return (
    <ProductCategoriesContext.Provider value={context}>
      {typeof children === 'function' ? children(context) : children}
    </ProductCategoriesContext.Provider>
  );
};
