import { GiftSelection, SelectedGift } from '@hooks/cart';

// change gift quantity by multiplyBy
export function multiplyGiftSelectionItemQuantity(gifts: GiftSelection[], multiplyBy: number) {
  return gifts.map(gift => {
    const newGiftSelectionItemGroup = gift.giftSelectionItemGroups.map(itemGroup => {
      const newGiftSelectionItems = itemGroup.giftSelectionItems.map(selectionItem => {
        return {
          ...selectionItem,
          quantity: (selectionItem?.quantity || 0) * multiplyBy,
        };
      });

      return { ...itemGroup, giftSelectionItems: newGiftSelectionItems };
    });

    return { ...gift, giftSelectionItemGroups: newGiftSelectionItemGroup };
  });
}

export function getGiftItemNumber(selectedGifts: SelectedGift[]) {
  return selectedGifts.reduce((acc, selectedGift) => {
    const { selectedGiftItems } = selectedGift;
    acc += selectedGiftItems.length;

    return acc;
  }, 0);
}
