import React, { useEffect, useCallback, useRef, useState } from 'react';
import { Input, message } from 'antd';
import { useRouter } from 'next/router';
import debounce from 'lodash/debounce';
import { ITEM_PER_PAGE } from '@utils/constants';

import { useGetVariantSearchLazyQuery } from '@hooks/search';
import { fireSearchEvent } from '@utils/gtmUtils';
import { getCurrPage } from '@utils/search';

const useSearchBar = () => {
  const [fetchVariantSearch, { loading, data }] = useGetVariantSearchLazyQuery();

  const router = useRouter();
  const query = (router.query?.q || '') as string;
  const [showResult, setShowResult] = useState(false);
  const [value, setValue] = useState(query);

  const searchRef = useRef<Input>(null);

  useEffect(() => {
    // sync query with value of search bar
    if (query !== value) setValue(query);
  }, [query]);

  const searchDebounced = useCallback(
    debounce((newQuery: string): void => {
      try {
        fetchVariantSearch({
          variables: {
            query: newQuery,
            first: ITEM_PER_PAGE,
            source: getCurrPage(router.pathname),
          },
        });
      } catch (error) {
        message.error(error.message);
      }

      // Handle GA - Tracking search
      fireSearchEvent(newQuery, 'Header');
    }, 500),
    [],
  );

  const handleFocus = useCallback(() => {
    setShowResult(true);
  }, []);

  const handleChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      if (!showResult) setShowResult(true);
      const value = e.target.value;
      setValue(value);
      searchDebounced(value);
    },
    [showResult],
  );

  const handleSearch = useCallback(
    (text: string) => {
      setValue(value);
      setShowResult(false);
      searchRef.current?.blur?.();
      router.push(`/search?q=${text}`);
    },
    [value],
  );

  return {
    searchRef,
    showResult,
    value,
    loading,
    data,
    setShowResult,
    handleFocus,
    handleChange,
    handleSearch,
  };
};

export default useSearchBar;
