import React, { memo, useCallback } from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { useRouter } from 'next/router';
import { Badge, Menu, Dropdown } from 'antd';
import { ShoppingCartOutlined, ArrowLeftOutlined } from '@ant-design/icons';
import get from 'lodash/get';
import cn from 'classnames';

import { useAuth } from '@context/AuthContext';
import TopNavHeader from '@components/TopNavHeader';
import NotificationCenter from '@components/NotificationCenter/NotificationCenter';
import Logo from '@components/Logo';
import { DotsVerticalIcon } from '@icons/DotsVertical';
import { useCartData } from '@hooks/cart';
import useHasMounted from '@hooks/useHasMounted';

import s from './index.module.less';

type MobileHeaderProps = {
  pageTitle?: React.ReactNode;
  backButtonTitle?: string;
  backButtonLink?: string;
  showBackButton?: boolean;
  hideCart?: boolean;
  transparent?: boolean;
};

const MobileHeader = memo(
  ({ pageTitle, backButtonTitle, backButtonLink, showBackButton, transparent, hideCart }: MobileHeaderProps) => {
    const router = useRouter();
    const { isLoggedIn } = useAuth();
    const hasMounted = useHasMounted();
    const { data } = useCartData();
    const totalCartItems = get(data, 'cart.orderItems.totalCount', 0);
    const isHomePage = router.pathname === '/';

    const renderTitle = useCallback(() => {
      if (backButtonTitle) return null;
      if (pageTitle) return <div className={s.pageTitle}>{pageTitle}</div>;
      return (
        <div className={s.contentContent}>
          <Link href="/" passHref>
            <a>
              <Logo className={s.logo} position="header" />
            </a>
          </Link>
        </div>
      );
    }, [backButtonTitle, pageTitle]);

    return (
      <TopNavHeader className={cn(s.topNavHeader, transparent && s.topNavTransparent)}>
        {!transparent && (
          <div className={s.background}>
            <div className={s.decoratingTriangleOne}>
              <Image src="/images/header-triangle-1.png" width="107" height="105" />
            </div>
            <div className={s.decoratingTriangleTwo}>
              <Image src="/images/header-triangle-2.png" width="137" height="52" />
            </div>
            <div className={s.decoratingTriangleThree}>
              <Image src="/images/header-triangle-3.png" width="180" height="54" />
            </div>
            <div className={s.decoratingTriangleFour}>
              <Image src="/images/header-triangle-4.png" width="94" height="54" />
            </div>
          </div>
        )}
        <div className={s.content}>
          <div className={s.leftContent}>
            {(showBackButton || !isHomePage) && (
              <>
                <a
                  onClick={() => {
                    if (backButtonLink) router.push(backButtonLink);
                    else router.back();
                  }}
                  className={cn(transparent && s.btnTransparent)}
                >
                  <ArrowLeftOutlined style={{ fontSize: 20 }} />
                </a>
                {backButtonTitle && <span className={s.backButtonTitle}>{backButtonTitle}</span>}
              </>
            )}
          </div>
          {renderTitle()}
          <div className={cn(s.rightContent, !backButtonTitle && s.flex1)}>
            <div className={s.space}>
              {isLoggedIn && hasMounted && !hideCart && (
                <>
                  <Link href="/cart" passHref>
                    <div className={cn(s.cart, transparent && s.btnTransparent)} data-test="cart-icon">
                      <Badge size="small" className={s.badge} count={totalCartItems} showZero>
                        <ShoppingCartOutlined style={{ fontSize: 24 }} />
                      </Badge>
                    </div>
                  </Link>
                  <NotificationCenter isMobile />
                </>
              )}
              <MoreDropdown isLoggedIn={isLoggedIn} transparent={transparent} />
            </div>
          </div>
        </div>
      </TopNavHeader>
    );
  },
);

const MoreDropdown = memo(({ isLoggedIn, transparent }: { isLoggedIn: boolean; transparent?: boolean }) => {
  const menuPaths = [
    {
      href: '/',
      name: 'Trang ch',
      label: 'Our product',
    },
    ...(isLoggedIn
      ? [
          {
            href: '/instantdeal',
            name: 't hng nhanh',
            label: 'Instant deal',
          },
          {
            href: '/account/navigator',
            name: 'Doanh nghip',
            label: 'Account',
          },
        ]
      : [
          {
            href: '/login',
            name: 'ng nhp',
            label: 'Login',
          },
          {
            href: '/register',
            name: 'ng k',
            label: 'Register',
          },
        ]),
  ];

  const menu = (
    <Menu className={s.menu}>
      {menuPaths.map(path => (
        <React.Fragment key={path.href}>
          <Menu.Item key={path.href}>
            <Link href={path.href} passHref>
              <a aria-label={path.label}>{path.name}</a>
            </Link>
          </Menu.Item>
          <Menu.Divider />
        </React.Fragment>
      ))}
    </Menu>
  );

  return (
    <Dropdown overlay={menu} trigger={['click']} arrow placement="bottomRight">
      <span className={cn(transparent && s.btnTransparent)}>
        <DotsVerticalIcon className={s.dotIcon} />
      </span>
    </Dropdown>
  );
});

export default MobileHeader;
