import React from 'react';
import Link from 'next/link';
import { Badge, Row, Col } from 'antd';
import { ShoppingCartOutlined } from '@ant-design/icons';
import get from 'lodash/get';

import CategoryMenu from '@components/CatergoryMenu';
import { useAuth } from '@context/AuthContext';
import SearchBar from '@components/SearchBar';

import { useCartData } from '@hooks/cart';
import { isBrowser } from '@utils/isBrowser';

import s from './Global.module.less';

const CategoryHeader = () => {
  const { isLoggedIn } = useAuth();
  const { data } = useCartData();
  const totalCartItems = get(data, 'cart.orderItems.totalCount', 0);

  const layouts = {
    category: {
      xs: 4,
      sm: 3,
      md: 6,
      lg: 6,
      xl: 6,
    },
    search: {
      xs: isLoggedIn ? 18 : 20,
      sm: isLoggedIn ? 19 : 21,
      md: isLoggedIn ? 14 : 18,
      lg: 14,
      xl: 14,
    },
    cart: {
      xs: isLoggedIn ? 2 : 0,
      sm: isLoggedIn ? 2 : 0,
      md: isLoggedIn ? 4 : 0,
      lg: 4,
      xl: 4,
    },
  };

  if (!isBrowser) return null;

  return (
    <div className={s.categoryHeader}>
      <div className={s.container}>
        <Row gutter={{ xs: 8, sm: 8, md: 24, lg: 24, xl: 24 }}>
          <Col {...layouts.category}>
            <CategoryMenu />
          </Col>
          <Col {...layouts.search} className={s.searchBar}>
            <SearchBar />
          </Col>
          <Col {...layouts.cart}>
            {isLoggedIn && (
              <Link href="/cart" passHref>
                <div className={s.cart} data-test="cart-icon">
                  <Badge className={s.badge} size="default" count={totalCartItems}>
                    <ShoppingCartOutlined style={{ width: 32, height: 32, fontSize: 32 }} />
                  </Badge>
                  {totalCartItems > 0 ? <span>{totalCartItems} sn phm</span> : ''}
                </div>
              </Link>
            )}
          </Col>
        </Row>
      </div>
    </div>
  );
};

export default CategoryHeader;
