import Link from 'next/link';
import { HomeOutlined, AppstoreOutlined, SearchOutlined, ThunderboltOutlined, UserOutlined } from '@ant-design/icons';
import { useRouter } from 'next/router';
import cn from 'classnames';

import * as s from './index.module.less';

const links = [
  {
    icon: HomeOutlined,
    label: 'Trang ch',
    href: '/',
  },
  {
    icon: AppstoreOutlined,
    label: 'Danh mc',
    href: '/categories',
  },
  {
    icon: ThunderboltOutlined,
    label: 't hng',
    href: '/instantdeal',
  },
  {
    icon: SearchOutlined,
    label: 'Tm kim',
    href: '/search',
  },
  {
    icon: UserOutlined,
    label: 'Ti khon',
    href: '/account/navigator',
  },
];

export default function BottomNavigator() {
  const router = useRouter();

  return (
    <div className={s.root}>
      {links.map(({ icon: Icon, ...link }) => (
        <Link href={link.href} passHref key={link.label}>
          <a className={cn(s.buttonNavigator, link.href === router.pathname && s.active)}>
            <div className={s.icon}>
              <Icon />
            </div>
            <span>{link.label}</span>
          </a>
        </Link>
      ))}
    </div>
  );
}
