import React from 'react';
import { Layout } from 'antd';
import Head from 'next/head';

import Header from '@layouts/Header';
import HeaderChrismas from '@layouts/HeaderChrismas';
import HeaderTet from '@layouts/HeaderTet';
import Footer from '@layouts/Footer';
import { getFestival } from '@utils/datetime';
import GiftsBtnNavigation from '@components/GiftsBtnNavigation';
import SaleAdminBanner from '@components/SaleAdminBanner';

import s from './index.module.less';
import { BasicLayoutProps } from './type';

const { Content } = Layout;

function renderHeader({ chrismas, tet }: { chrismas?: boolean; tet?: boolean }) {
  if (chrismas) return <HeaderChrismas />;
  if (tet) return <HeaderTet />;

  return <Header />;
}

const BasicLayout = ({ title = 'This is the default title', children }: BasicLayoutProps) => {
  const chrismas = getFestival() === 'chrismas';
  const tet = getFestival() === 'tet';

  return (
    <div>
      <Head>
        <title>{title}</title>
        <meta charSet="utf-8" />
        <meta name="viewport" content="initial-scale=1.0, width=device-width" />
      </Head>
      <Layout className={s.root}>
        {renderHeader({ chrismas, tet })}
        <div className={s.container}>
          <SaleAdminBanner />
        </div>
        <GiftsBtnNavigation />
        <Content>{children}</Content>
        <Footer festival={getFestival()} />
      </Layout>
    </div>
  );
};

export default BasicLayout;
